.wrapper{
  padding-top: 5px;
}
.img-section{
    width: 100%;
    height: 360px;
    position: relative;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    color: #fff;
    background-size:cover;
 }
 .home-content{
   width: 100%;
   height: auto;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   background-color: transparent;
   padding-top: 100px;
 }
 .title{
   margin-bottom: 15px;
   margin-top: 15px;
   font-size: 15px;
   line-height: 16px;
   font-weight: 600;
 }
 .about-site > h2{
   font-size: 36px;
   line-height: 44px;
   font-weight: 400;
   font-family: 'Varela Round', sans-serif;
 }
 .about-site > p {
   font-family: 'Varela Round', sans-serif;
   font-weight: 400;
   margin-bottom: 28px;
   font-size: 1rem;
   line-height: 30px;
 }
 .about-site{
  font-family: 'Varela Round', sans-serif;
  font-size: 28px;
  line-height: 36px;
  font-weight: 400;
  text-align: center;
  color: #fff;
 }